import React, { Component } from 'react';
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag';
import styled from 'styled-components';
import { USER_LIKES_QUERY } from './Likes';

const DELETE_LIKE_MUTATION = gql`
    mutation DELETE_LIKE_MUTATION($id: ID!) {
        deleteAddToLikes(id: $id) {
            id
        }
    }
`;

const RemoveButton = styled.button`
    color: red;
`;

class RemoveLike extends Component {
    update = (cache, payload) => {
        const data = cache.readQuery({ query: USER_LIKES_QUERY });
        data.likes = data.likes.filter(like => like.id !== payload.data.deleteAddToLikes.id);
        cache.writeQuery({ query: USER_LIKES_QUERY, data });
    }
    render() {
        return (
            <Mutation
                mutation={DELETE_LIKE_MUTATION}
                variables={{ id: this.props.id }}
                update={this.update}
            >
                {(deleteAddToLikes, { error }) => {
                    console.log(this.props.id);
                    return (
                        <RemoveButton onClick={() => {
                            deleteAddToLikes().catch(err => {
                                alert(err.message);
                            });
                        }}>
                            Like
                        </RemoveButton>
                    )
                }}
            </Mutation>
        )
    }
}

export default RemoveLike;